import Vue from 'vue';
import set from 'lodash/set';
import repos from 'json!../repos.json';

const Repo = Vue.extend({
  props: ['repo'],
  data() {
    const [authorName, repoName] = this.repo.full_name.split('/');
    return {authorName, repoName};
  },
  template:
`<li class="repo-item">
  <div class="repo-full-name">
    <span class="repo-name">{{repoName}}</span>
    <span class="repo-author-name">/ {{authorName}}</span>
  </div>
  <ol class="repo-tags">
    <li class="repo-tag-item" v-for="tag in repo.tags">{{tag.text}}</li>
  </ol>
  <div class="repo-desc">{{repo.description}}</div>
</li>`
});

const RepoList = Vue.extend({
  props: ['repos'],
  template:
`<ol class="repos">
  <repo v-for="repo in repos" :repo="repo"></repo>
</ol>`,
  components: {
    'repo': Repo,
  },
});

console.time('INITIAL');
new Vue({
  el: '#app',
  data: {repos},
  ready() {
    setTimeout(() => {
      console.time('DISPATCH');
      this.$set('repos[0].tags[0]', {id: 213, text: 'Node.js'});
      // the DOM didn't really change at this point
      console.timeEnd('DISPATCH');
    }, 1000);
  },
  template: `<div><repo-list :repos="repos"></repo-list></div>`,
  components: {
    'repo-list': RepoList,
  }
});
console.timeEnd('INITIAL');




